obre que rodea completamente el botón del cuerno o el cuerno pequeño, esto suele aparecer a los 10 a 20 seg., donde se desprende un olor desagradable de pelo y piel chamuscado. Se repite en el otro cuerno. Se emplea en terneros de 1 a 5 semanas. (Goicochea, 2016). ")])]),_c('div',{staticClass:"col-md-5"},[_c('figure',[_c('img',{staticClass:"img-t m-auto",attrs:{"src":require("@/assets/curso/tema7/img17.svg"),"alt":"Texto que describa la imagen"}})])])])]),_c('div',{staticClass:"py-4 py-md-5",attrs:{"titulo":"Cauterizador metálico"}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("Cauterizador metálico ")]),_c('p',[_vm._v("Es uno de los más populares en el país por su facilidad de traslado, la edad del ternero, económico, donde se realiza el descorné por el calor y la fricción, donde se alistan 2 tubos de 25 a 30 cm de acero o cobre. Se calientan los tubos al rojo vivió, se debe sujetar adecuadamente el animal y alistar la zona retirando el pelo para tener una mejor visual, posterior a esto se realiza el acercamiento del tubo a la yema del cuerno y mediante movimientos giratorios se cauteriza el rudimento del cuerno, se debe ejercer esta acción durante 5 a 10 segundos hasta que aparezca una costra de color café el desprendimiento se da a los 25 - 30 días. ")])]),_c('div',{staticClass:"col-md-5"},[_c('figure',[_c('img',{staticClass:"img-t m-auto",attrs:{"src":require("@/assets/curso/tema7/img18.svg"),"alt":"Texto que describa la imagen"}})])])])]),_c('div',{staticClass:"py-4 py-md-5",attrs:{"titulo":"Descolmillada"}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("Descolmillada")]),_c('p',[_vm._v("El manejo en las crías es importante tanto para el bienestar de la madre como de las crías por esta razón se realizan labores como el descolmillado en las producciones porcícolas, debido a que los cerdos nacen con unos dientes caninos muy afilados donde estos dientes pueden causar lesiones en las ubres de las cerdas provocando una negación al amamantamiento y por consiguiente un retraso en el crecimiento de los mismos, así como un problema en la cerda para las camadas próximas. El momento oportuno para realizar el descolmillado es 6 horas después del nacimiento de los lechones, asegurando el consumo suficiente de calostro para asegurar que su sistema inmune esté listo. Es importante contar con las herramientas adecuadas como lo son un corta colmillos afilados un antiséptico preferiblemente yodado, y una buena técnica al realizar el corte. Este procedimiento se realiza con unos instrumentales previamente desinfectados, colocar el corta colmillos paralelos a la mandíbula y cortar el diente sin ir a cortar la mandíbula, es importante después de realizar el proceso marcar el lechón y verificar que no queden dientes agudos. ")])]),_c('div',{staticClass:"col-md-5"},[_c('figure',{staticClass:"mt-5"},[_c('img',{staticClass:"img-t m-auto",attrs:{"src":require("@/assets/curso/tema7/img19.svg"),"alt":"Texto que describa la imagen"}})])])])]),_c('div',{staticClass:"py-4 py-md-5",attrs:{"titulo":"Corte de la cola"}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("Corte de la cola")]),_c('p',[_vm._v("Este procedimiento es muy utilizado en la porcicultura y en avicultura para evitar futuros problemas de bienestar animal ya que los animales con colas en etapas adultas son comúnmente mordidos por otros animales donde estas mordeduras producen lesiones e infecciones a nivel de la columna vertebral, abscesos y fuertes dolores y para el productor una penalización de la canal al momento del sacrificio. Este corte de la cola se realiza después del consumo de calostro más o menos entre las 12 y las 72 horas. Como previo al uso todos los elementos deben estar previamente desinfectados y el procedimiento se debe realizar por manos expertas donde se toma al animal con una mano y se extiende la cola se calcula una distancia más o menos de 16 mm y se realiza un corte limpio y rápido. Otra técnica consiste en realizar el corte utilizando un cauterizador especial. Si se llega a producir un sangrado se recomienda realizar un torniquete durante 15 minutos. ")])]),_c('div',{staticClass:"col-md-5"},[_c('figure',{staticClass:"mt-5"},[_c('img',{staticClass:"img-t mx-auto",attrs:{"src":require("@/assets/curso/tema7/img20.svg"),"alt":"Texto que describa la imagen"}})])])])]),_c('div',{staticClass:"py-4 py-md-5",attrs:{"titulo":"Castración"}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("Castración")]),_c('p',[_vm._v("En las diferentes producciones pecuarias una de las prácticas de manejo animal más reconocido es la castración de los machos, ya que animales que no van a ser destinados a la reproducción y presentan temperamento muy agresivo su manejo se dificulta mucho por ende se opta por la castración. La castración es la acción de anular el funcionamiento de los testículos del macho ya sea por la extracción o por inhibición de esperma y hormonas sexuales masculinas. ")]),_c('p',[_vm._v("La edad inicial para realizar la castración varía dependiendo de la especie el objetivo de los animales y por último el sistema productivo. En los cerdos es de gran importancia realizar la castración ya que estos producen un fuerte olor sexual que al momento del sacrificio se impregna en la carne dando un sabor y olor fuerte de la misma que al consumidor no le agrada, por esta razón la castración en los cerdos se realiza de manera prácticamente obligada desde las 2 o 3 semanas de edad. En el ganado bovino la castración se realiza más por manejo animal, se recomienda realizar la castración desde los 6 y 9 meses de edad. En cabras se recomienda de 2 a 5 meses, la castración en los equinos puede realizarse a cualquier edad, pero lo más recomendable es entre 2 y 3 años. ")]